<script>
import TableComponent from "@/components/Table/Table.vue";

export default {
  name: "workAreaSignoffStaff-component",
  components: { TableComponent },
  data() {
    return {
      workAreaForm: {
        fac: "", // 廠區
        location: "", // 主區域
        fab: "", // 廠別
        fab_Name: "", // 廠別(名稱)
        LD1: "", // 廠別(id)
        SECOND_AREA_NAME: "", // 次區域
        SECOND_AREA_ID: "",
        Add_Sign: "",
        EditMode: "0", // 0: 新增
      },
      workAreaTableColumns: [
        { id: "index", label: "項次", width: "70" },
        { id: "fac", label: "廠區代號", width: "150" },
        { id: "location", label: "主區域", width: "150" },
        { id: "fab", label: "FAB廠別", width: "150" },
        { id: "second_Area_Name", label: "次區域", width: "150" },
        { id: "second_Area_Id", label: "次區域系統編號", width: "175" },
      ],
      selectedWorkArea: [], // 刪除工作區域
      editWorkArea: false,
      editWorkAreaItem: {}, // 修改工作區域
      editWorkAreaItemRules: {
        second_Area_Name: [
          { required: true, message: "必填", trigger: "change" },
        ],
      },
      workAreaPage: 1, // 工作區域表格的頁碼

      signoffStaffForm: {
        fac: "", // 廠區
        location: "", // 主區域
        area_Id: "", // 次區域
        area_Type: "", // 通知人員
        emp_No: "", // 員工工號
      },
      workerLoading: false,
      search: "",
      signoffStaffTableColumns: [
        { id: "index", label: "項次", width: "75" },
        { id: "locInfo", label: "廠區代號/主區域/FAB廠別", width: "240" },
        { id: "area_Description", label: "次區域(系統編號)", width: "175" },
        { id: "area_Type_Name", label: "通知人員種類", width: "150" },
        { id: "pCount", label: "人數", width: "75" },
        { id: "nameList", label: "員工姓名", width: "200" },
      ],
      delSignoff: false,
      delStaff: [], // 刪除簽核人員
      // signoffStaffPage: 1, // 簽核人員表格的頁碼
    };
  },
  computed: {
    facList() {
      return this.$store.state.workArea_facList;
    },
    locationList() {
      return this.$store.state.workArea_locationList;
    },
    // 依據廠區、主區域，列出廠別清單
    fabList() {
      if (this.workAreaForm.fac && this.workAreaForm.location) {
        return this.$store.state.workArea_fabList.filter(
          (e) =>
            e.ld1 == this.workAreaForm.fac.ld1 &&
            e.location_Type_Id == this.workAreaForm.location.id
        );
      } else {
        return [];
      }
    },
    // 工作區域清單 (All)
    workArea() {
      return this.$store.state.workArea_all;
    },
    // 工作區域清單 (單頁)
    workAreaList() {
      let res = this.$store.state.workArea_allList;
      res.forEach((e) => {
        e.fac = this.workAreaForm.fac.ld2;
        e.location = this.workAreaForm.location.description;
        e.fab = this.workAreaForm.fab_Name;
      });
      return res;
    },

    secondAreaList() {
      return this.$store.state.signoff_secondAreaList;
    },
    areaTypeList() {
      return this.$store.state.signoff_areaType;
    },
    workerList() {
      return this.$store.state.signoff_workerList;
    },
    // 篩選搜尋之內容 (主區域、次區域)
    signoffStaff() {
      if (!this.search) {
        return this.$store.state.signoff_all;
      } else {
        return this.$store.state.signoff_all.filter(
          (e) =>
            e.locInfo.includes(this.search) ||
            e.area_Description.includes(this.search)
        );
      }
    },
    // signoffStaffList() {
    //   return this.$store.state.signoff_allList;
    // },
    // 欲刪除的簽核人員列表
    signoffDetail() {
      return this.$store.state.signoff_detail;
    },
  },
  methods: {
    // 一更改廠區或主區域，就清空廠別
    chgWorkAreaHandler() {
      this.workAreaForm.fab = "";
    },
    async qryWorkArea() {
      if (!this.workAreaForm.fab) {
        this.$message({
          type: "error",
          showClose: true,
          message: "請選擇廠別",
        });
      } else {
        try {
          // 取出 fab.id 送表單，並更新清單上的 fab_Name
          this.workAreaForm.fab_Name = this.workAreaForm.fab.floor;
          await this.$store.dispatch("getWorkArea", this.workAreaForm.fab.id);

          // this.$refs.workAreaForm.resetFields();
          this.workAreaPage = 1;
        } catch (err) {
          console.log(err);
        }
      }
    },
    async addWorkArea() {
      if (!this.workAreaForm.fab || !this.workAreaForm.SECOND_AREA_NAME) {
        this.$message({
          type: "error",
          showClose: true,
          message: "請輸入廠別及次區域名稱",
        });
      } else {
        try {
          // 取出 fab.id 送表單，並更新清單上的 fab_Name
          this.workAreaForm.fab_Name = this.workAreaForm.fab.floor;
          this.workAreaForm.LD1 = this.workAreaForm.fab.id;
          const res = await this.$store.dispatch(
            "addWorkArea",
            this.workAreaForm
          );
          if (res.data.isOK) {
            this.$message({
              type: "success",
              duration: 5000,
              showClose: true,
              message: "新增成功",
            });

            // 清空表單，更新工作區域清單 (呈現第一頁)
            // this.$refs.workAreaForm.resetFields();
            this.signoffStaffForm.fac = "";
            this.signoffStaffForm.location = "";
            this.$store.commit("CURRENT_PAGE_INIT");
            this.workAreaPage = 1;
          }
        } catch (err) {
          console.log(err);
        }
      }
    },
    changeSelection(item) {
      this.selectedWorkArea = item;
    },
    async delWorkArea() {
      this.selectedWorkArea.forEach((e) => {
        e.LD1 = e.location_Id;
      });
      try {
        await this.$store.dispatch("delWorkArea", this.selectedWorkArea);

        // 取消所有勾選，更新工作區域清單 (呈現第一頁)
        this.$refs.TableComponent.$refs.multipleTable.clearSelection();
        this.selectedWorkArea = [];
        this.workAreaPage = 1;
      } catch (err) {
        console.log(err);
      }
    },

    // 開啟視窗，並代入相應的資訊
    editHandler(item) {
      this.editWorkAreaItem = { ...item };
      this.editWorkAreaItem.add_Sign = this.editWorkAreaItem.add_Sign == "Y";
      this.editWorkArea = true;
    },
    async updWorkArea() {
      this.target = {};
      this.target.SECOND_AREA_ID = this.editWorkAreaItem.second_Area_Id;
      this.target.SECOND_AREA_NAME = this.editWorkAreaItem.second_Area_Name;
      this.target.LD1 = this.editWorkAreaItem.location_Id;
      this.target.Add_Sign = this.editWorkAreaItem.add_Sign ? "Y" : "";
      this.target.EditMode = "1";

      try {
        const res = await this.$store.dispatch("addWorkArea", this.target);
        if (res.data.isOK) {
          this.$message({
            type: "success",
            duration: 5000,
            showClose: true,
            message: "修改成功",
          });

          // 關閉視窗，並更新工作區域清單 (呈現第一頁)
          this.editWorkArea = false;
          // this.editWorkAreaItem = {};
          this.workAreaPage = 1;
        }
      } catch (err) {
        console.log(err);
      }
    },
    workAreaChangePage(page) {
      this.workAreaPage = page;
      this.$store.commit("CHANGE_PAGE_HANDLER", {
        page,
        paramsDataName: "workArea_all",
        paramsListName: "workArea_allList",
      });
    },

    // 一更改廠區或主區域，就重取次區域
    async chgSignoffStaffHandler() {
      if (this.signoffStaffForm.fac && this.signoffStaffForm.location) {
        try {
          await this.$store.dispatch("getSecondArea", {
            fac: this.signoffStaffForm.fac,
            location: this.signoffStaffForm.location,
          });
        } catch (err) {
          console.log(err);
        }
      }
    },
    // 按 enter 後蒐取員工清單
    async workerSearchHandler(evt) {
      this.workerLoading = true;
      if (evt.key == "Enter") {
        this.workerLoading = false;
        if (!evt.target.value) return;
        this.$store.dispatch("getWorkerList", evt.target.value);
      }
    },
    async qrySignoffStaff() {
      if (!this.signoffStaffForm.fac || !this.signoffStaffForm.location) {
        this.$message({
          type: "error",
          showClose: true,
          message: "請選擇廠區及主區域",
        });
      } else {
        try {
          await this.$store.dispatch("getSignoffStaff", {
            fac: this.signoffStaffForm.fac,
            location: this.signoffStaffForm.location,
          });

          // this.$refs.signoffStaffForm.resetFields();
          this.signoffStaffPage = 1;
        } catch (err) {
          console.log(err);
        }
      }
    },
    async addSignoffStaff() {
      if (
        !this.signoffStaffForm.area_Id ||
        !this.signoffStaffForm.area_Type ||
        !this.signoffStaffForm.emp_No
      ) {
        this.$message({
          type: "error",
          showClose: true,
          message: "請輸入次區域、通知人員及員工工號",
        });
      } else {
        try {
          let item = {};
          item.area_Id = this.signoffStaffForm.area_Id;
          item.area_Type = this.signoffStaffForm.area_Type;
          item.emp_No = this.signoffStaffForm.emp_No;

          const res = await this.$store.dispatch("addSignoffStaff", item);
          if (res.data.isOK) {
            this.$message({
              type: "success",
              duration: 5000,
              showClose: true,
              message: "新增成功",
            });

            // 清空表單，更新簽核人員清單 (呈現第一頁)
            // this.$refs.signoffStaffForm.resetFields();
            this.qrySignoffStaff();
          }
        } catch (err) {
          console.log(err);
        }
      }
    },

    // 開啟視窗，並代入相應的資訊
    async delHandler(item) {
      this.delSignoff = true;
      try {
        await this.$store.dispatch("getSignoffStaffDetail", {
          area_Id: item.area_Id,
          ld1: item.ld1,
          location_Type_Id: item.location_Type_Id,
          area_Type: item.area_Type,
        });
      } catch (err) {
        console.log(err);
      }
    },
    async delSignoffStaff() {
      try {
        await this.$store.dispatch("delSignoffStaff", this.delStaff);

        // 關閉視窗，並更新簽核人員清單 (呈現第一頁)
        this.delSignoff = false;
        this.delStaff = [];
        this.qrySignoffStaff();
      } catch (err) {
        console.log(err);
      }
    },
    // 簽核人員不分頁
    // signoffStaffChangePage(page) {
    //   this.signoffStaffPage = page;
    //   this.$store.commit("CHANGE_PAGE_HANDLER", {
    //     page,
    //     paramsDataName: "signoff_all",
    //     paramsListName: "signoff_allList",
    //   });
    // },
  },
  async mounted() {
    this.$store.commit("HEADER_HANDLER", true);
    this.$store.commit("BREADCRUMB_HANDLER", true);
    this.$root.$children[0].sidebarSwitch = true;

    try {
      // 清空先前的查詢結果
      this.$store.state.workArea_all = [];
      this.$store.state.workArea_allList = [];
      this.$store.state.signoff_workerList = [];
      this.$store.state.signoff_all = [];

      // 打 API 取得廠區、主區域資料
      await this.$store.dispatch("getWorkAreaCondition");
      await this.$store.dispatch("getSignoffStaffCondition");
    } catch (err) {
      console.log(err);
    }
  },
};
</script>

<template src="./workAreaSignoffStaff.html"></template>
